import React, { Component } from 'react';
import './PageStart.css';

import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'

import {selectAddExpensePage} from '../actions/PageStart.js';

class PageStart extends Component {
	constructor(props) {
		super(props);
	}

  render() {
  	let classes = "page";
    if(this.props.currentPage == "start"){
      classes += " active";
    }

    return (
      <div className={classes}>
			<div className="row">
				<div className="col"><a className="button error" onClick={this.props.selectAddExpensePage}><span className="icons very-very-big"></span></a></div>
			</div>
      </div>
    );
  }
}

function mapStateToProps(state){
	return { 
    	currentPage: state.currentPage
	};
}

function matchDispatchToProps(dispatch){
  return bindActionCreators({ selectAddExpensePage: selectAddExpensePage }, dispatch);
}

export default connect(mapStateToProps, matchDispatchToProps)(PageStart);
